*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    height: 100vh; /* Make sure the body covers the entire viewport height */
    background-color: cadetblue;
 
}

.Maindiv{
    width: 100%;

}
.childdiv{
    width:600px;
    max-width: 600px;   
    background-color: azure;
    text-align: center;
    padding: 5%;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 5px 5px 50px #224849;   
}
.additems{
    background: #fff;
    border: 1px solid;
    margin: 10px 0;
}  

.showitem{
    margin-top: 10px;
}
.item-list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #8cc4d0;
    padding: 10px 14px;
    border-radius:5px;
    margin: 10px 0;

}
.remove-btn{
    background: #8cc4d0;
    padding: 15px 20px;
    border-radius: 5px;
    border: none;
}
input{
    width:90%;
    padding: 10px;  
    border: none;
    outline: none;
}